footer {
  text-align: center;
  padding: 5px;
  color: $dark-grey;
  background-color: $clear-black;
  .contact {
    margin-top: 20px;

    .phone,
    .email {
      padding: 5px 100px;
    }
  }

  .icones .fab {
    font-size: 35px;
    margin: 25px 10px;
    transition: color 0.1s;

    &:hover {
      color: $secondary;
    }
  }

  .copyright {
    font-size: 14px;
  }
}
