.container-projects {
  @extend %flexCenter;
  flex-direction: column;
  padding: 5vw 0;

  .project {
    position: relative;
    width: 60%;
    margin-bottom: 50px;

    a {
      display: flex;
      align-items: center;
      border-radius: 5px;
      transition: transform 0.2s;
      background-color: $clear-black;
      color: $clear-grey;
      box-shadow: 0 0 5px rgb(192, 192, 192);

      &:hover {
        transform: scale(1.05);
        box-shadow: 0 0 3px rgb(204, 204, 204);

        .project-img .show,
        .project-img::after {
          opacity: 1;
        }
      }
      .project-img {
        position: relative;
        width: 30%;
        img {
          border-radius: 5px;

          min-width: 130px;
        }

        .show {
          z-index: 100;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          text-align: center;
          color: $secondary;
          opacity: 0;
          transition: opacity 0.2s;
          font-size: clamp(10px, 1.5vw, 15px);
          text-shadow: 0 0 5px black;
          font-weight: 700;
        }

        &::after {
          content: "";
          position: absolute;
          top: 0;
          display: block;
          width: 100%;
          height: 100%;
          border-radius: 5px;
          background-color: rgba(0, 0, 0, 0.8);
          opacity: 0;
          transition: opacity 0.2s;
        }
      }

      .project-infos {
        margin: auto;
        width: 60%;
        display: flex;
        flex-direction: column;

        p {
          font-size: clamp(10px, 1.8vw, 16px);
          em {
            color: $secondary;
          }
          .em-title {
            font-weight: bold;
            font-size: 1.5em;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .container-projects .project {
    width: 85%;

    img {
      width: 75%;
      margin: auto;
    }
  }
}
@media screen and (max-width: 1000px) {
  .container-projects .project {
    a {
      flex-direction: column;
      height: 60vw;

      .project-img {
        width: 100%;
      }
      .project-infos {
        margin: 0;
        width: 100%;
        padding: 20px;
        display: flex;

        justify-content: center;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .container-projects .project {
    a {
      height: fit-content;
      img {
        width: 100%;
      }
    }
  }
}
