nav {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
  background-color: $black;
  color: $clear-grey;

  @extend %flexCenter;
  a {
    position: relative;
    font-size: clamp(12px, 1.5vw, 16px);
    margin: 20px 2vw;
    transition: color 0.2s 0.2s;
    text-shadow: 0 0 5px black;

    &::after {
      content: "";
      display: block;
      position: absolute;
      height: 2px;
      width: 0%;
      background-color: $secondary;
      transition: width 0.2s, color 0.1s;
      opacity: 0;
    }

    &:hover {
      color: $secondary;
      &::after {
        width: 100%;
        opacity: 1;
      }
    }

    &.nav-active {
      color: $secondary;

      &::after {
        width: 100%;
        opacity: 1;
      }
    }
  }
}
.download {
  position: absolute;
  padding: 5px;
  border-radius: 5px;
  color: $primary;
  background-color: #fff;

  right: 5%;
  white-space: nowrap;
  text-shadow: none;

  transition: background 0.2s;

  &::after {
    height: 0;
  }

  &:hover {
    color: $primary;
    background-color: $dark-grey;
    i {
      transition: color 0.2s;
      color: $secondary;
    }
  }
}

@media screen and (max-width: 600px) {
  .download {
    position: inherit;
  }
}
