.container-form {
  padding: 5vw 0;
  background-color: #646864;

  form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
    padding-bottom: 25px;
    color: $primary;
    text-align: center;

    #mail,
    #message,
    #name,
    button {
      margin-bottom: 10px;
      color: $dark-grey;
      font-size: 17px;
      border: none;
      outline: none;
      padding: 5px;
      background: hsl(120, 2%, 25%);
      border-bottom: 3px solid $secondary;
      resize: none;
      transition: all 0.2s;

      &:hover {
        background: hsl(120, 2%, 30%);
      }

      &::placeholder {
        color: hsl(0, 0%, 42%);
      }
    }
    #mail,
    #message,
    #name {
      &:focus {
        background: hsl(120, 2%, 30%);
      }
    }

    label,
    button {
      font-weight: 600;
      margin: 10px 0;
      color: $clear-black;
      font-family: "Roboto", sans-serif;
    }

    button {
      cursor: pointer;
      padding: 10px;
      color: hsl(0, 0%, 52%);

      &:hover {
        background: hsl(120, 2%, 30%);
      }

      &:active {
        box-shadow: inset 0 2px 4px $black;
      }
    }

    .form-message {
      display: block;
      font-weight: initial;
      position: absolute;
      bottom: 0px;
      animation: wiggle 1s;
    }
  }
}

@keyframes wiggle {
  0% {
    transform: translate(0px, 0px);
  }

  25% {
    transform: translate(-10px, 0px);
  }
  50% {
    transform: translate(10px, 0px);
  }
  75% {
    transform: translate(-10px, 0px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}

@media screen and (max-width: 1000px) {
  .container-form form {
    width: 70%;
  }
}

@media screen and (max-width: 700px) {
  .container-form form {
    width: 80%;
  }
}
