.container-presentation {
  @extend %flexCenter;
  padding: 5vw 0;

  .txt {
    font-size: clamp(16px, 1.5vw, 22px);
    width: 55%;
    color: $clear-black;

    p {
      margin: 20px 0;
    }
  }
  .link {
    text-decoration: underline;
    font-weight: 600;
  }
}

@media screen and (max-width: 700px) {
  .container .txt {
    width: 80%;
  }
}
