.container-home {
  background: url("../../assets/background.svg") no-repeat center center/cover;
  height: fit-content;
  margin-bottom: 5vw;
  padding: 100px 0 50px 0;

  .content-home {
    @extend %flexCenter;
    flex-direction: column;
    min-height: 100%;
    padding: 50px 0;

    .name,
    .skills,
    .secondary-skills {
      height: fit-content;
      white-space: nowrap;
    }

    .name {
      font-size: clamp(0.8em, 2vw, 1.5em);
      color: $clear-grey;
      text-align: center;
    }

    .skills {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 60%;

      color: white;

      .skill {
        width: 70%;
      }
      .secondary-skills {
        min-width: 140px;
        width: 20%;
        max-width: 200px;
        display: flex;
        flex-wrap: wrap;
        justify-content: end;
        transition-delay: all 1s;

        li.card-skill {
          font-size: 13px;
          border-radius: 5px;
          padding: 8px;
          margin: 5px 5px 0 0;
          background-color: $secondary;
          color: $black;
          font-weight: 600;
          font-size: clamp(10px, 1vw, 13px);
        }
      }

      ul li {
        h3 {
          min-width: 70px;
          font-size: clamp(12px, 1.5vw, 16px);
        }

        .level {
          position: relative;
          min-width: 75px;
          width: 100%;
          height: 7px;
          margin-right: 5px;
          border-radius: 1px;
          background-color: rgb(235, 235, 235);
          overflow: hidden;
          box-shadow: 0 0 1px 0 white;
          margin-bottom: 10px;

          &::after {
            content: "";
            position: absolute;
            top: 0;
            display: block;
            height: 100%;
            box-shadow: 0px 0px 10px 2px black;
            animation: grow 2s 0.5s forwards ease-in-out;
          }
          &.html::after {
            max-width: 95%;
            background-color: #dd4d25;
          }
          &.css::after {
            max-width: 90%;
            background-color: #146eb0;
          }

          &.js::after {
            max-width: 85%;
            background-color: #efd81d;
          }
          &.react::after {
            max-width: 85%;
            background-color: #00d5f7;
          }
          &.next::after {
            max-width: 60%;
            background-color: #000;
          }
          &.node::after {
            max-width: 40%;
            background-color: #026e00;
          }
        }
      }
    }
  }
}

@keyframes grow {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@media screen and (max-width: 1500px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 45%;
    }
  }
}
@media screen and (max-width: 950px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 60%;
    }
  }
}
@media screen and (max-width: 600px) {
  .container-home .content-home {
    .name,
    .skills,
    .secondary-skills {
      width: 80%;
    }
  }
}
@media screen and (max-width: 350px) {
  .container-home .content-home {
    .name h1 {
      white-space: normal;
    }
    .name,
    .skills,
    .secondary-skills {
      width: 95%;
    }
  }
}
